.slider_wrap {
  width: 100%;
}
.slider_wrap .slider_deep {
  position: relative;
  width: calc(100% / 2);
  margin: auto;
  color: #00001a;
  opacity: 1;
}
.slider_wrap .slider_deep-card {
  height: calc(100vw / 3.8);
  max-height: 350px;
}
.slider_wrap .slider_deep-card_background {
  width: 96%;
  padding-bottom: calc(100vh / 2.5);
  position: absolute;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% center;
  opacity: 0.7;
  z-index: -1;
}
.slider_wrap .slider_deep-card_content {
  padding: 0 20px;
  display: flex;
  flex-direction: column;
}
.slider_wrap .slider_deep-card_content .btn {
  width: fit-content;
  margin: auto;
  z-index: 1;
}
.slider_wrap .slider_deep .slider__next {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  position: absolute;
  background-color: #833ab4;
  border-color: #f5f5f5;
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg width='30' height='53' viewBox='0 0 30 53' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 51l25-24.5L2 2' stroke='%23969696' stroke-width='3'/%3E%3C/svg%3E");
  background-position: 0;
  right: -9%;
  top: 50%;
}
.slider_wrap .slider_deep .slider__next:hover {
  background-color: #542574;
  border-color: #f5f5f5;
}
.slider_wrap .slider_deep .slider__prev {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  position: absolute;
  background-color: #833ab4;
  border-color: #f5f5f5;
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg width='30' height='53' viewBox='0 0 30 53' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M28 2L3 26.5 28 51' stroke='%23969696' stroke-width='3'/%3E%3C/svg%3E");
  background-position: 0;
  background-repeat: no-repeat;
  left: -6%;
  top: 50%;
}
.slider_wrap .slider_deep .slider__prev:hover {
  background-color: #542574;
  border-color: #f5f5f5;
}
