.entry_page .container_animate {
  position: fixed;
  width: 200px;
  height: 45vh;
  transform: translate(-11vw, -120vh);
}
.entry_page .container_animate .ball {
  width: 20px;
  height: 20px;
  margin: 30px auto;
  border-radius: 50px;
  opacity: 0.7;
}
.entry_page .container_animate .ball:nth-child(1) {
  animation: right 1s infinite ease-in-out;
}
.entry_page .container_animate .ball:nth-child(2) {
  animation: left 1.1s infinite ease-in-out;
}
.entry_page .container_animate .ball:nth-child(3) {
  animation: right 1.05s infinite ease-in-out;
}
.entry_page .container_animate .ball:nth-child(4) {
  animation: left 1.15s infinite ease-in-out;
}
.entry_page .container_animate .ball:nth-child(5) {
  animation: right 1.1s infinite ease-in-out;
}
.entry_page .container_animate .ball:nth-child(6) {
  animation: left 1.05s infinite ease-in-out;
}
.entry_page .container_animate .ball:nth-child(7) {
  animation: right 1s infinite ease-in-out;
}
.entry_page .container_animate .ball:nth-child(8) {
  animation: left 1.1s infinite ease-in-out;
}
